.pagination {
  display: block;
  font: 400 14px Roboto,sans-serif;
  padding-top: 20px;

  &__btn {
    display: inline-block;
    text-decoration: none;
    color: #0D47A1;
    padding: 3px 10px;
    border-radius: 3px;
    border: 1px solid #fff;

    &_active {
      border-color: #b8d6ed;
      color: #000;
    }

    &:hover {
      color: #1260da;
    }

  }

}
