.header {
  background-color: #aadd12;
  height: 80px;

  &__logo {
    display: inline-block;
    margin: 7px 0 7px 35px;
    width: 200px;
    height: 65px;
    background: url(../images/logo.png);
  }

  &__search{
    display: inline-block;
    position: relative;
    width: calc(100% - 560px);
    //max-width: 1300px;
    vertical-align: top;
    margin: 20px 20px;
  }

  &__login {
    display: inline-block;
    margin: 0 60px 0 0;
    width: 200px;
    vertical-align: top;
    float: right;
    text-align: right;

    &-btn {
      display:inline-block;
      font: 700 16px Roboto,sans-serif;
      color: #000;
      text-decoration: none;
      margin: 20px 0;
      padding: 10px 20px;
      border-radius: 5px;
      transition: background .2s;

      &:hover {
        background-color: #fff;
      }

    }

  }

  &__user {
    position: relative;
    display: inline-block;
    margin: 15px 60px 0 0;
    width: 200px;
    float: right;


    &-btn {
      vertical-align: top;
      border-radius: 50%;
      cursor: pointer;
      text-align: right;

      &:hover {
        color: #0D47A1;
      }

    }

    &-name {
      vertical-align: top;
      padding: 15px 10px 0 0;
      display: inline-block;
      font: 400 16px Roboto,sans-serif;
      color: #000;
    }

    &-img {
      display: inline-block;
      border-radius: 50%;
      width: 50px;
      height: 50px;

      &:hover {
        opacity: .9;
      }

      &:active {
        opacity: .8;
      }

    }

    &-menu {
      display: none;
      position: absolute;
      z-index: 1;
      top: 55px;
      right: 0;
      font: 400 16px Roboto,sans-serif;
      background-color: #fff;
      border-radius: 5px;
      box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
      padding: 5px 0;

      &_open {
        display: block;
      }

      &-btn {
        display: block;
        padding: 5px 15px;
        color: #000;
        text-decoration: none;

        &:hover {
          background-color: #f5f5f5;
        }

      }

      &-logout {
        display: block;
        color: #000;
        padding: 5px 10px;
        margin: 10px 15px 5px 15px;
        text-align: center;
        background: #fff;
        border-radius: 5px;
        border: 1px solid #d8d8d8;
        text-decoration: none;
        transition: color .2s,background .1s;

        &:active {
          background-color: #ededed;
        }

      }

    }

  }

}

.search-field {
  display: block;
  width: 100%;
  padding: 10px 10px 10px 50px;
  border-radius: 5px;
  border-style: none;
  outline: 0;
  font: 400 16px Roboto,sans-serif;
  background-color: #d5ee89;
  box-shadow: 0 1px 0 0 #83ab0e;
  color: #000;
  transition: background .2s;

  &::-webkit-input-placeholder { color: #689F38; }

  &:-moz-placeholder { color: #689F38; }

  &::-moz-placeholder { color: #689F38; }

  &:-ms-input-placeholder { color: #689F38; }

  &:focus {
    color: #000;
    background-color: #fff;
  }

}

.search-icon {
  position: absolute;
  top: 12px;
  left: 20px;
  color: #689F38;
}
