.product-container {
  display: block;
  height: auto;

  &_ptr1 {
    height: 235px;
    width: 100%;
    background: url(../images/ptr1.png) repeat;
  }

  &_ptr2 {
    height: 235px;
    width: 100%;
    background: url(../images/ptr2.png) repeat;
  }

}

.product-table {
  width: 100%;
  height: 29px;
  padding-left: 30px;

  &_bd1 {
    display: inline-block;
    float: left;
    width:14px;
    height: 29px;
    background: url(../images/bd1.png) no-repeat;
  }

  &_bd2 {
    display: inline-block;
    float: left;
    width:calc(100% - 14px);
    height: 29px;
    background: url(../images/bd2.png) repeat;
  }

}

.product-wrapper {
  padding: 10px 40px;
}

.product-item {
  display: inline-block;
  position: relative;
  margin: 10px 0 0 10px;
  border-radius: 5px;
  color: #000;
  transition: box-shadow .2s, background .2s;
  max-width: 210px;
  height: 270px;

  &_result {
    background: #fff;
    vertical-align: top;
  }

  &:hover {
    background: #fff;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  }

  &__img {
    position: relative;
    padding: 20px 10px 0 10px;
    cursor: pointer;
    width: 210px;
    max-width: 210px;
    max-height: 250px;
  }

  &__price-header {
    position: absolute;
    top: 10px;
    right: 10px;
    background: #aadd12;
    padding: 6px 10px;
    border-radius: 0 5px 5px 0;
    font: 500 18px Roboto,sans-serif;
    color: #fff;
    white-space: nowrap;

    &_through {
      text-decoration: line-through;
      padding-right: 10px;
    }

    &:before {
      position: absolute;
      top: 0;
      right: 100%;
      width: 0;
      height: 0;
      content: '';
      border: solid transparent;
      border-width: 18px;
      border-color: rgba(89, 89, 89, 0);
      border-right-color: #aadd12;
      pointer-events: none;
    }

    //& .arrow-left {
    //  display: inline-block;
    //  position: absolute;
    //  left: -17px;
    //  top: 0px;
    //  width: 0;
    //  height: 0;
    //  border-top: 18px solid transparent;
    //  border-bottom: 18px solid transparent;
    //  border-right: 18px solid #aadd12;
    //}

    & .circle {
      width: 8px;
      height: 8px;
      background-color: #ffffff;
      position: absolute;
      border-radius: 50%;
      top: 14px;
      left: -7px;
    }

  }

  &__like {
    display: inline-block;
    position: absolute;
    top: 47px;
    right: 10px;
    padding: 3px 10px;
    font: 500 12px Roboto,sans-serif;
    color: #996633;
    background: #fec151;
    border-radius: 5px;

    & i {
      color: #fff;
      padding-right: 5px;
    }

  }

  &__add {
    position: absolute;
    bottom: 57px;
    right: 10px;
    color: #fff;
    background: #51c91c;
    width: 40px;
    height: 40px;
    font-size: 14px;
    text-align: center;
    border-radius: 50%;
    cursor: pointer;
    transition: all .2s;

    & i {
      position: relative;
      top: 13px;
    }

    &:hover {
      box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }

    &:active {
      background: #43a617;
    }

  }

  &__price-footer{
    position: absolute;
    bottom: 53px;
    right: 60px;
    padding: 3px 10px;
    border-radius: 0 5px 5px 0;
    font: 500 12px Roboto,sans-serif;
    color: #996633;
    background: #fec151;
    white-space: nowrap;

    &_through {
      text-decoration: line-through;
      padding-right: 10px;
    }

    &:before {
      position: absolute;
      top: 0;
      right: 100%;
      width: 0;
      height: 0;
      content: '';
      border: solid transparent;
      border-width: 11px;
      border-color: rgba(89, 89, 89, 0);
      border-right-color: #fec151;
      pointer-events: none;
    }

    //& .arrow-left {
    //  display: inline-block;
    //  position: absolute;
    //  left: -12px;
    //  top: 0px;
    //  width: 0;
    //  height: 0;
    //  border-top: 11px solid transparent;
    //  border-bottom: 12px solid transparent;
    //  border-right: 12px solid #fec151;
    //}

    & .circle {
      width: 5px;
      height: 5px;
      background-color: #ffffff;
      position: absolute;
      border-radius: 50%;
      top: 9px;
      left: -5px;
    }

  }

  &__dist {
    display: inline-block;
    position: absolute;
    bottom: 76px;
    right: 60px;
    padding: 3px 10px;
    font: 500 12px Roboto,sans-serif;
    color: #996633;
    background: #fec151;
    border-radius: 5px;

    & i {
      color: #fff;
      padding-right: 5px;
    }
  }

  &__title {
    position: absolute;
    width: 100%;
    bottom: 0;
    padding: 10px 10px 0 10px;
    text-align: center;
    font: 700 12px Roboto,sans-serif;
    color: #000;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    max-width: 210px;
    border-top: 1px solid #e7e7e7;
  }

}


.product {

  &-retailers {
    width: 50%;
    height: 100%;
    padding: 20px;
    float: left;
    overflow: auto;
    background-color: #fff;
    border-radius: 5px 0 0 5px;

    &__tabs-container {
      height: 500px;
      border-radius: 5px;
      background-color: #fff;

      &:after {
        content: " ";
        visibility: hidden;
        display: block;
        height: 0;
        clear: both;
      }

    }

    &__container {
      position: absolute;
      width: calc(100% - 120px);
      height: calc(100% - 251px);
      min-height: 500px;
      margin: 0 60px 60px 60px;
      //padding: 20px;
      border-radius: 5px;
      //background-color: #fff;

      &:after {
        content: " ";
        visibility: hidden;
        display: block;
        height: 0;
        clear: both;
      }

    }

    &__list {
      position: relative;
      //margin-right: 20px;
      width: 100%;
    }

    &__map {
      width: 50%;
      height: 100%;
      float: left;
      //padding: 20px 20px 20px 0;
      background-color: #fff;
      border-radius: 0 5px 5px 0;

      &_padding {
        padding: 20px 20px 20px 0;
      }

      & #map {
        width: 100%;
        height: 100%;
      }

    }

    &__sort {
      display: block;
      padding-bottom: 20px;

      &-label {
        display: inline-block;
        font: 400 12px Roboto,sans-serif;
        color: #000;
      }

      &-btn {
        position: relative;
        display: inline-block;
        padding: 3px 7px;
        border-radius: 3px;
        font: 400 12px Roboto,sans-serif;
        color: #0D47A1;
        text-decoration: none;
        white-space: nowrap;
        border: 1px solid #fff;

        &_active {
          padding-right: 30px;
          border-color: #b8d6ed;
          color: #000;

          &:after {
            position: absolute;
            top: 50%;
            right: 7px;
            width: 10px;
            height: 10px;
            margin-top: -5px;
            content: "";
            text-align: center;
            background-image: url('../images/drop.svg');
            background-repeat: no-repeat;
            background-position: 50% 50%;
          }

          &_increase {

            &:after {
              transform: rotate(180deg);
            }

          }

        }

        &:hover {
          color: #1260da;
        }

      }

    }

  }

  &-retailer {
    display: table;
    width: 100%;

    &__item {
      padding: 20px 10px;
      //margin-right: 20px;
      border-radius: 5px;
      cursor: pointer;
      transition: background .1s;;

      &:hover {
        background-color: #f5f5f5;
      }

      &:active {
        background-color: #ededed;
      }

    }



    &__count {
      display: table-cell;
      vertical-align: middle;
      font: 500 14px Roboto, sans-serif;
      color: #b1b3bc;
      padding-right: 10px;
    }

    &__img {
      display: table-cell;
      vertical-align: middle;
      width: 100px;
      padding-right: 10px;

      & .image {
        width: 100px;
        height: auto;
      }

    }

    &__info {
      display: table-cell;
      vertical-align: top;
      padding-right: 10px;
    }

    &__title {
      display: inline-block;
      font: 700 14px Roboto, sans-serif;
      color: #000;
      padding: 0;
      margin: 0 5px 0 0;
    }

    &__reviews {
      font: 500 14px Roboto, sans-serif;
      color: #b1b3bc;
    }

    &__addr {
      font: 500 14px Roboto, sans-serif;
      color: #b1b3bc;
    }

    &__price {
      font: 500 14px Roboto, sans-serif;
      color: #e06125;
      padding-top: 10px;
    }

    &__distance {
      display: table-cell;
      vertical-align: middle;
      white-space: nowrap;
      font: 500 14px Roboto, sans-serif;
      color: #757575;

      & .icon {
        font-size: 20px;
        color: #3fb166;
      }

    }

  }

}

